@media (min-width: $desktop-w) {
  .list-pm {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    font-family: "Open Sans", "Calibri", sans-serif;
    color: $text-btn;
    padding: 0 10%;
  }
  .list-pm__elem {
    width: 366px;
    background-color: rgba(45, 41, 41, 0.5);
    margin-right: 16px;
    margin-bottom: 40px;
    display: flex;
    flex-direction: column;
  }
  .list-pm__title {
    font-weight: 700;
    font-size: 24px;
    padding-top: 20px;
    padding-left: 30px;
    padding-right: 70px;
    text-align: left;
    letter-spacing: -2px;
  }
  .list-pm__text {
    padding-top: 12px;
    padding-bottom: 12px;
    font-size: 18px;
    padding-left: 30px;
    padding-right: 30px;
    text-align: left;
    letter-spacing: -1px;
  }
  .list-pm__wrap {
  }
  .list-pm__logo {
  }
}

