.header {
  background-image: url("../img/backFirst_mob.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding-bottom: 30px;
}

.header__wrap {
  padding-top: 15px;
  padding-left: 15px;
  width: 100%;
}

.header__logo {
  width: 60%;
  height: 60%;
}

.header__title-wrap {
  margin: 0 auto;
  text-align: center;
  width: 100%;
}

.header__title {
  font-weight: 700;
  margin-bottom: 25px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 32px;
}
.header__text {
  font-size: 12px;
  text-align: center;
  letter-spacing: -1px;
  line-height: 17px;
  padding-left: 40px;
  padding-right: 40px;
}
.header__link {
  display: block;
  align-items: center;
  margin: 0 auto;
  width: 249px;
  height: 56px;
  text-decoration: none;
  color: $text-btn;
  font-family: "Open Sans", "Calibri", sans-serif;
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  background-color: $main-col;
  text-align: center;
  text-transform: uppercase;
  padding-top: 12px;
  margin-top: 35px;
  border-radius: 10px;
}

@media (min-width: $desktop-w) {
  .header {
    background-image: url("../img/backFirst.jpg");
    padding-bottom: 30px;
  }

  .header__wrap {
    padding-top: 30px;
    padding-left: 162px;
  }

  .header__logo {
    width: 15%;
    height: 15%;
  }

  .header__title-wrap {
    width: 52%;
    margin-bottom: 100px;
  }

  .header__title {
    margin-bottom: 25px;
    font-size: 48px;
  }
  .header__text {
    padding: 0;
    font-size: 0.9em;
    text-align: center;
  }

  .header__link {
    width: 290px;
    height: 65px;
    text-decoration: none;
    font-size: 24px;
    line-height: 50px;
    font-weight: 700;
    padding-top: 5px;
    margin-top: 35px;
  }
}









