.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  font-family: "Open Sans", "Calibri", sans-serif;
  color: $text-btn;
  padding: 0 5%;
}

.list__elem {
  width: 290px;
  background-color: rgba(45, 41, 41, 0.5);
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
}

.list__title {
  font-weight: 700;
  font-size: 14px;
  line-height: 15px;
  padding-top: 25px;
  padding-left: 20px;
  padding-bottom: 25px;
  padding-right: 33%;
  letter-spacing: -1px;
}
.list__text {
  display: none;
}

.list__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: auto;
  padding-top: 10px;
  padding-right: 5%;
  &--team {
    background-color: $team;
  }
  &--finance {
    background-color: $finance;
  }
  &--system-think {
    background-color: $system-think;
  }
  &--desing-think {
    background-color: $desing-think;
  }
  &--leader {
    background-color: $leader;
  }
}
.list__link {
  text-decoration: none;
  color: $text-btn;
  font-size: 14px;
  line-height: 15px;
  &:hover {
    color: $global-col;
  }
}

.list__logo {
  width: 24px;
  height: 24px;
  margin-left: 15px;
  &--team {
    width: 26px;
    height: 28px;
  }
}

@media (min-width: $desktop-w) {
  .list {
    flex-direction: row;
    justify-content: space-evenly;
    padding: 0 10%;
  }
  .list__elem {
    position: relative;
    width: 366px;
    margin-bottom: 40px;

    &--system-think {
      margin-left: auto;
      margin-right: 45px;
    }
    &--team  {
      margin-right: auto;
    }
  }
  .list__title {
    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
    padding-top: 20px;
    padding-left: 30px;
    padding-right: 70px;
    text-align: left;
  }
  .list__text {
    display: block;
    padding-top: 0;
    padding-bottom: 33px;
    font-size: 18px;
    line-height: 26px;
    padding-left: 30px;
    padding-right: 30px;
    text-align: left;
    letter-spacing: -1px;
  }
  .list__wrap {
    padding-top: 10px;
    padding-bottom: 10px;
    &--team {
      padding-top: 6.5px;
      padding-bottom: 6.5px;
    }
  }
  .list__link {
    display: block;
    height: 30px;
    margin-top: 2%;
    font-size: 18px;
    line-height: 26px;
    letter-spacing: -1px;
  }

  .list__logo {
    width: 37px;
    height: 37px;
    margin-left: 30px;
    &--team {
      width: 39px;
      height: 44px;
    }
  }
}

