.footer {
  display: flex;
  flex-direction: column;
  background-color: $footer-col;
}
.footer__list {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin: 0 auto;
}

.footer__elem {
  margin-left: 6px;
  margin-right: 6px;
  margin-top: 17px;
}

@media (min-width: $desktop-w) {
  .footer {
    flex-direction: row;
    height: 200px;
  }

  .footer__list {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto;
  }

  .footer__elem {
    margin-left: 50px;
    margin-right: 32px;
  }
}


