.about-programm {
  background-image: url("../img/back_second.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding-bottom: 34px;
}

.about-programm__wrap {
  padding-top: 20px;
  padding-left: 20px;
  width: 100%;
}
.about-programm__logo {
  width: 55%;
  height: 55%;
}

.about-programm__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 700;
  text-align: center;
  color: $text-btn;
  margin-bottom: 20px;
}

.about-programm__link {
  display: block;
  align-items: center;
  margin: 0 auto;
  width: 249px;
  height: 56px;
  text-decoration: none;
  color: $text-btn;
  font-family: "Open Sans", "Calibri", sans-serif;
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  background-color: $main-col;
  text-align: center;
  text-transform: uppercase;
  padding-top: 12px;
  margin-top: 35px;
  margin-bottom: 34px;
  border-radius: 10px;
}

@media (min-width: $desktop-w) {
  .about-programm__wrap {
    padding-top: 30px;
    padding-left: 100px;
    width: 100%;
  }
  .about-programm__logo {
    width: 15%;
    height: 15%;
  }

  .about-programm__title {
    font-size: 48px;
    margin-bottom: 100px;
  }

  .about-programm__link {
    width: 290px;
    height: 65px;
    font-size: 24px;
    line-height: 50px;
    font-weight: 700;
    padding-top: 5px;
    margin-top: 35px;
    border-radius: 10px;
  }
}


