.programm-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
  font-size: 0.9em;
  margin-top: 20px;
}

.programm-list__elem {
  font-family: "Open Sans", "Calibri", sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  width: 100%;
  position: relative;
  margin-top: 30px;
  &::after {
    content: "";
    position: absolute;
    width: 55px;
    height: 55px;
    top: -10px;
    left: 13%;
    background-color: $main-col;
    border-radius: 80px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 23px 23px;
  }
  &--ileader::after {
    background-image: url("../img/iLeader.svg");
  }
  &--team::after {
    background-image: url("../img/team.svg");
  }
  &--finance::after {
    background-image: url("../img/finance.svg");
  }
  &--desing-thinking::after {
    background-image: url("../img/design_thinking.svg");
  }
  &--system-thinking::after {
    background-image: url("../img/system_thinking.svg");
  }
}

.programm-list__text {
  padding-left: 35%;
  padding-right: 30px;
}

@media (min-width: $desktop-w) {
  .programm-list {
    flex-direction: row;
    font-size: 0.9em;
  }

  .programm-list__elem {
    font-family: "Open Sans", "Calibri", sans-serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    width: 38%;
    position: relative;
    margin-top: 30px;
    &--finance {
      padding-left: 225px;
    }
    &--desing-thinking {
      padding-right: 225px;
    }
    &--system-thinking{
      margin: 0 auto;
      padding-left: 131px;
      padding-right: 131px;
    }
    &::after {
      content: "";
      position: absolute;
      width: 100px;
      height: 100px;
      top: 2px;
      left: 41%;
      background-color: $main-col;
      border-radius: 80px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: 43px 43px;
    }
    &--ileader::after {
      background-image: url("../img/iLeader.svg");
    }
    &--team::after {
      background-image: url("../img/team.svg");
      background-size: 45px 50px;
    }
    &--finance::after {
      left: 61%;
      background-image: url("../img/finance.svg");
    }
    &--desing-thinking::after {
      left: 22%;
      background-image: url("../img/design_thinking.svg");
    }
    &--system-thinking::after {
      background-image: url("../img/system_thinking.svg");
    }
  }

  .programm-list__text {
    text-align: center;
    margin-top: 105px;
    padding-left: 0;
    padding-right: 0;
  }
}

