.trainers {
  display: none;
}

@media (min-width: $desktop-w) {
  .trainers {
    background-image: url("../img/circles_bot.svg");
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: 0 100%;
    margin-top: 264px;
    display: flex;
    justify-content: space-around;
    position: relative;
    padding-bottom: 45px;
  }

  .trainers__img {
    position: absolute;
    width: 100%;
    height: 727px;
    left: 0;
    top: 0;
  }

  .trainers__title {
    background-color: $main-col;
    color: $global-col;
    font-size: 66px;
    line-height: 65px;
    font-family: "Open Sans", "Calibri", sans-serif;
    letter-spacing: -1px;
    width: 44%;
    text-align: left;
    margin-left: 167px;
    padding-left: 60px;
    padding-top:  158px;
    padding-bottom: 157px;
  }
  .trainers__text {
    background-color: rgba(201, 216, 220, 0.9);
    font-family: "Open Sans", "Calibri", sans-serif;
    font-size: 24px;
    line-height: 33px;
    letter-spacing: -1px;
    width: 43%;
    max-height: 355px;
    text-align: left;
    margin-right: 167px;
    margin-left: -155px;
    margin-top: 45px;
    padding-left: 38px;
    padding-right: 38px;
    padding-top: 76px;
  }//

  .trainers__text-head {
    font-size: 36px;
  }
}

