.how-do {
  display: none;
}

@media (min-width: $desktop-w) {
  .how-do {
    display: block;
    background-color: $global-col;
    color: $text-btn;
    text-align: center;
    padding: 0 173px;
    position: relative;
    overflow: visible;
  }

  .how-do__img {
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
  }

  .how-do__text {
    font-family: "Open Sans", "Calibri", sans-serif;
    font-size: 36px;
    font-weight: 400;
    padding-top: 78px;
    padding-bottom: 105px;
    line-height: 52px;
  }
}

