.form__title {
  font-weight: 700;
  font-size: 14px;
  line-height: 15px;
  padding-top: 25px;
  padding-bottom: 25px;
  letter-spacing: -1px;
  text-align: center;
}
.form__text {
  font-size: 14px;
  line-height: 18px;
  padding: 10px;
}
.form__frame {
  display: none;
  &--leader {
    display: block;
  }
  &--finance .form__item + .form__group-text {
    &::before {
      background-color: $finance;
    }
  }
  &--desing-think .form__item + .form__group-text {
    &::before {
      background-color: $desing-think;
    }
  }
  &--system-think .form__item + .form__group-text {
    &::before {
      background-color: $system-think;
    }
  }
  &--team .form__item + .form__group-text {
    &::before {
      background-color: $team;
    }
  }
}

.form__block {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.form__wrap {
  position: relative;
  width: 31%;
  height: 17%;
  margin: 10px;
}

.form__item {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.form__group-text {
  display: inline-block;
  width: 100%;
  height: 120px;
}

.form__item + .form__group-text {
  position: relative;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    background-color: $leader;
  }

  &:hover {
    &::before {
      box-shadow: 0 0 20px $text-btn;
    }
  }

  &:active {
    &::before {
      box-shadow: 0 0 20px $text-btn;
    }
  }
}

.form__item:checked + .form__group-text {
  &::before  {
    box-shadow: 0 0 20px $text-btn;
  }
}

.form__time-wrap {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: left;
  left: 15%;
  top: 5%;
  &--second {
    left: 63%;
  }
  &--third {
    top: 57%;
  }
  &--fourth {
    left: 63%;
    top: 57%;
  }
}

.form__time {
  font-size: 0.8rem;
  height: 20px;
}

.form__btn {
  align-items: center;
  margin: 0 auto;
  width: 290px;
  height: 65px;
  text-decoration: none;
  color: $text-btn;
  font-family: "Open Sans", "Calibri", sans-serif;
  font-size: 24px;
  line-height: 50px;
  font-weight: 700;
  background-color: $main-col;
  text-align: center;
  text-transform: uppercase;
  padding-top: 5px;
  margin-top: 35px;
  border-radius: 10px;
}

.form__description {
  margin: 50px auto;
  width: 100%;
  font-size: 14px;
  line-height: 26px;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 35px;
  padding-bottom: 35px;
  background-color: rgba(45, 41, 41, 0.5);
}

.success, .error {
  display: none;
  position: absolute;
  background-color: rgba(45, 41, 41, 0.9);
  padding: 20px;
  height: 150%;
  z-index: 10;
}

.form__ok {
  align-items: center;
  margin: 0 auto;
  width: 290px;
  height: 65px;
  text-decoration: none;
  color: $text-btn;
  font-family: "Open Sans", "Calibri", sans-serif;
  font-size: 24px;
  line-height: 50px;
  font-weight: 700;
  background-color: $main-col;
  text-align: center;
  text-transform: uppercase;
  padding-top: 5px;
  margin-top: 35px;
  border-radius: 10px;
}

.form__err {
  display: block;
  align-items: center;
  margin: 0 auto;
  width: 290px;
  height: 65px;
  text-decoration: none;
  color: $text-btn;
  font-family: "Open Sans", "Calibri", sans-serif;
  font-size: 18px;
  line-height: 50px;
  font-weight: 700;
  background-color: $main-col;
  text-align: center;
  text-transform: uppercase;
  padding-top: 5px;
  margin-top: 35px;
  border-radius: 10px;
}

@media (min-width: $desktop-w) {
  .form__title {
    font-size: 22px;
    line-height: 22px;
    padding-top: 50px;
    padding-bottom: 50px;
    padding-left: 30px;
    padding-right: 30px;
    text-align: center;
  }
  .form__text {
    font-size: 22px;
    line-height: 26px;
    padding-left: 150px;
    padding-right: 150px;
  }

  .form__wrap {
    margin: 5%;
    margin-left: 14%;
    width: 300px;
    height: 225px;
  }

  .form__group-text {
    width: 300px;
    height: 225px;
  }

  .form__time-wrap {
    display: flex;
    flex-direction: column;
    text-align: left;
    left: 23%;
    top: 13%;
    &--second {
      left: 73%;
    }
    &--third {
      top: 63%;
    }
    &--fourth {
      left: 73%;
      top: 63%;
    }
  }
  .form__time {
    font-size: 18px;
    height: 100%;
  }

  .form__description {
    margin: 70px auto;
    width: 687px;
    font-size: 18px;
    line-height: 26px;
    padding-left: 80px;
    padding-right: 80px;
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .success, .error {
    width: 70%;
    height: 180%;
  }

  .success__message, .error__message {
    margin-top: 30%;
  }
}
