.record-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-evenly;
  font-family: "Open Sans", "Calibri", sans-serif;
  color: $text-btn;
}

.record-list__elem {
  width: 290px;
  background-color: rgba(45, 41, 41, 0.5);
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  &--leader {
    border: 3px solid $leader;
  }
}

.record-list__title {
  font-weight: 700;
  font-size: 14px;
  line-height: 15px;
  padding-top: 25px;
  padding-left: 20px;
  padding-bottom: 25px;
  padding-right: 33%;
  letter-spacing: -1px;
}

.record-list__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: auto;
  padding-top: 10px;
  padding-right: 5%;
  &--team {
    background-color: $team;
  }
  &--finance {
    background-color: $finance;
  }
  &--system-think {
    background-color: $system-think;
  }
  &--desing-think {
    background-color: $desing-think;
  }
  &--leader {
    background-color: $leader;
  }
}

.record-list__logo {
  width: 24px;
  height: 24px;
  margin-left: 15px;
  &--team {
    width: 26px;
    height: 28px;
  }
}

.record-form {
  width: 100%;
  text-align: center;
  color: $text-btn;
  background-color: $record-background;
}


@media (min-width: $desktop-w) {
  .record-list {
    padding: 0 5%;
  }
  .record-list__elem {
    width: 300px;
    height: 255;
    margin-bottom: 63px;
    &--team {
      margin-bottom: 0;
    }
  }

  .record-list__title {
    font-size: 22px;
    line-height: 22px;
    padding-top: 50px;
    padding-bottom: 50px;
    padding-left: 30px;
    padding-right: 30px;
    text-align: left;
  }

  .record-list__wrap {
    padding-top: 10px;
    padding-bottom: 10px;
    &--team {
      padding-top: 6.5px;
      padding-bottom: 6.5px;
    }
  }

  .record-list__logo {
    width: 37px;
    height: 37px;
    margin-left: 30px;
    &--team {
      width: 39px;
      height: 44px;
    }
  }

  .record-form {
    width: 993px;
  }
}
