.calendar {
  max-width: 1440px;
  margin: 0 auto;
  background-image: url("../img/back_second.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.calendar__header {
  padding-bottom: 30px;
}

.calendar__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 700;
  text-align: center;
  color: $text-btn;
  margin-bottom: 20px;
}
.calendar__wrap {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

@media (min-width: $desktop-w) {
  .calendar__wrap {
    padding-bottom: 200px;
    flex-wrap: nowrap;
  }

  .calendar__title {
    font-size: 48px;
    margin-bottom: 100px;
  }
}


